<template>
    <div id="takeout-notify">
        <public-header title="通知提醒"></public-header>
        <div class="content">
            <van-radio-group>
                <van-cell-group>
                    <van-cell title="通知店员规则" />
                    <van-cell class="border-0px">
                        <div slot="title">下单后未接单,则微信模板消息/商家APP语音通知</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_clerk.notify_delay"><span>分</span>
                            </div>
                        </div>
                    </van-cell>
                    <van-cell class="border-0px">
                        <div slot="title">通知频率</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_clerk.notify_frequency"><span>分</span>
                            </div>
                        </div>
                    </van-cell>
                    <van-cell class="border-0px">
                        <div slot="title">总通知次数</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_clerk.notify_total"><span>次</span>
                            </div>
                        </div>
                    </van-cell>
                    <van-cell class="border-0px">
                        <div slot="title">通知第几次时同时电话通知</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_clerk.notify_phonecall_time"><span>次</span>
                            </div>
                        </div>
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
            <van-radio-group class="margin-10-t">
                <van-cell-group>
                    <van-cell title="通知配送员规则" />
                    <van-cell class="border-0px">
                        <div slot="title">下单后未接单,则微信模板消息/商家APP语音通知</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_deliveryer.notify_delay"><span>分</span>
                            </div>
                        </div>
                    </van-cell>
                    <van-cell class="border-0px">
                        <div slot="title">通知频率</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_deliveryer.notify_frequency"><span>分</span>
                            </div>
                        </div>
                    </van-cell>
                    <van-cell class="border-0px">
                        <div slot="title">总通知次数</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_deliveryer.notify_total"><span>次</span>
                            </div>
                        </div>
                    </van-cell>
                    <van-cell class="border-0px">
                        <div slot="title">通知第几次时同时电话通知</div>
                        <div slot="right-icon">
                            <div class="input-unit">
                                <input type="number" v-model="order.notify_rule_deliveryer.notify_phonecall_time"><span>次</span>
                            </div>
                        </div>
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				order:{
					notify_rule_clerk: {
						notify_delay: '0',
						notify_frequency: '0',
						notify_phonecall_time: '0',
						notify_total: '0'
					},
					notify_rule_deliveryer: {
						notify_delay: '0',
						notify_frequency: '0',
						notify_phonecall_time: '0',
						notify_total: '0'
					}
				},
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/agent/config/order'
				});
			},

			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/agent/config/order',
					data: that.order
				});
            }
		},
        mounted(){
			this.onLoad();
        }
	}
</script>

<style>

</style>


